@font-face {
  font-weight: 400;
  font-family: "PTMono";
  font-style: normal;
  src: url("../fonts/PTMono-Regular.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "RobotoCondensed";
  font-style: bold;
  src: url("../fonts/RobotoCondensed-Bold.woff") format("woff"); }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  padding: 0;
  margin: 0;
  font-family: "PTMono", "Arial Narrow", sans-serif;
  font-size: 16px;
  line-height: 18px;
  color: #ffffff;
  background-color: #1a2931;
  background-size: 320px auto;
  counter-reset: section; }

a {
  text-decoration: none; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0); }

.button {
  display: block;
  width: 287px;
  height: 55px;
  font-family: inherit;
  color: #ffffff;
  background-color: #00b2ff;
  text-transform: uppercase;
  border: 0;
  text-align: center;
  padding: 17px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }
  .button:focus, .button:hover {
    background-color: #1a2931; }
  .button:active {
    background-color: #1a2931;
    color: #ffffff; }

.container {
  position: relative;
  display: block;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 0;
  padding-right: 0;
  background-size: 100%; }

@media (min-width: 320px) {
  .container {
    background-image: url("../img/bg-mobile.png"); } }

@media (min-width: 768px) {
  .container {
    background-image: url("../img/bg-tablet.png"); } }

@media (min-width: 1024px) {
  .container {
    background-image: none; } }

.main-nav {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row; }

.main-nav__toggle {
  position: relative;
  display: inline-block;
  cursor: pointer;
  outline: none;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  background: transparent;
  padding: 0;
  font-size: inherit;
  font-family: inherit; }
  .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 10px;
    right: 27px;
    width: 37px;
    height: 13px;
    background-image: url("../img/icon-menu.svg");
    background-repeat: no-repeat; }

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  border: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-top: 5px;
  height: 35px;
  width: 90px; }

.main-nav-list__item-call,
.main-nav-list__item-link-info,
.main-nav-list__item-link-call {
  display: none; }

@media (min-width: 768px) {
  .main-nav__list {
    padding-top: 15px;
    height: 37px;
    width: 82px; }
  .main-nav__toggle::before {
    top: 18px;
    right: 37px; } }

@media (min-width: 1024px) {
  .main-nav-list__item-link {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
